// tslint:disable:object-literal-sort-keys
// tslint:disable:no-console
import * as React from "react";
import * as imgdata from '../img/img.json';
import './Gallery.css';
import { ImgData } from './Model.js';

class GalleryStat {
  public imagesData: ImgData[];
  public imagesOffset:number;
  public colGroup:string[][];
  public colHeight:number[];
  public blur:boolean;
};

class GalleryProp{
  public onImgSel:(img:string)=>void;
}

// tslint:disable-next-line:max-classes-per-file
export default class Gallery extends React.Component<GalleryProp, GalleryStat> {
  public constructor(prep: GalleryProp) {
    super(prep);

    let column = this.getWindowColumn();
    let colHeight = new Array(column);
    colHeight.fill(0);

    this.state = {
      imagesData: imgdata,
      imagesOffset: 0,
      colGroup: [],
      colHeight: [] = colHeight,
      blur:false,
    };
  }

  public render() {
    const colGroup = this.state.colGroup;
    return (
      <div id="gallery" className={(this.state.blur?"blur":"")}>
        {
          colGroup.map((group,i)=>(
            <div className="gallery-col" key={i}>
              {
                group.map((img,j)=>(
                  <img onClick={this.onImageClick} data-file={img} className={"gallery-img "} key={j} src={ require('../artworks-s/'+img)} />
                ))
              }
            </div>
          ))
        }
      </div>
    );
  }

  public onImageClick = (e: React.MouseEvent<HTMLImageElement>)=>{
    const cb = this.props.onImgSel;
    if(cb == null) return;
    const target = e.target;
    if(target == null) return ;
    let img = target as HTMLImageElement;
    let imgfile = img.dataset['file'];
    if(imgfile != null){
      cb(imgfile);
    }
  }

  public componentDidMount(){
    this.trackDocumentSize();
    this.loadImage(16);
    document.addEventListener('scroll', this.trackScrolling);
    window.addEventListener('resize',this.trackDocumentSize);
  }

  public componentWillUnmount() {
    document.removeEventListener('scroll', this.trackScrolling);
    window.removeEventListener('resize',this.trackDocumentSize);
  }

  private getWindowColumn(): number {
    const windowWith = window.innerWidth;
    if (windowWith < 576) {
      return 1;
    }
    else if (windowWith < 992) {
      return 2;
    }
    else {
      return 3;
    }
  }

  private trackDocumentSize =()=>{
    const wrappedElement = document.getElementById('gallery');
    if(wrappedElement == null) return;
    let columnSize = this.getWindowColumn();
    this.setColumn(columnSize);
  }

  private trackScrolling = () => {
    const wrappedElement = document.getElementById('gallery');
    if(wrappedElement != null){
      const bottom = wrappedElement.getBoundingClientRect().bottom;
      const wbottom = window.innerHeight;

      if(bottom < wbottom+ 20.0){
        this.loadImage(3);
      }
    }
  };


  private setColumn(col:number){
    const state = this.state;
    let curCol = state.colHeight.length;
    if(curCol == col || col <1 || col >3) return;

    let newColHeight = new Array(col);
    newColHeight.fill(0);

    let newCol = new Array(col);
    for(let i=0;i<col;i++) newCol[i] = [];

    Gallery.FillImage(newCol,newColHeight,state.imagesData.slice(0,state.imagesOffset));
    
    this.setState({
      colHeight: newColHeight,
      colGroup: newCol
    });
  }

  private static FillImage(groups:string[][],colHeight:number[],imgs:ImgData[],margin:number = 20){
    const colCount = colHeight.length;
    for(let n=0;n<imgs.length;n++){
      let colindex = 0;
      let colh = colHeight[0];

      for(let i=1;i< colCount;i++){
        if(colHeight[i] < colh){
          colindex = i;
          colh = colHeight[i];
        }
      }

      let curimg = imgs[n];
      groups[colindex].push(curimg.file);
      colHeight[colindex] += curimg.h;
    }
  }

  public blur(enable:boolean){
    this.setState({
      blur:enable
    });
  }

  private loadImage(mincount:number = 6){
    const state = this.state;
    const imgAll = state.imagesData;
    const totalImgLength = imgAll.length;
    let imgOffset = this.state.imagesOffset;
    if(imgOffset == totalImgLength) return;

    let colHeight = state.colHeight;
    let colGroup = state.colGroup;
    const colCount = colHeight.length;

    while(colGroup.length < colCount){
      colGroup.push([]);
    }

    let addCount = Math.min(mincount,totalImgLength - imgOffset);
    let subset  = imgAll.slice(imgOffset,imgOffset + addCount);
    Gallery.FillImage(colGroup,colHeight,subset);
    imgOffset+= subset.length;

    this.setState({
      colGroup: colGroup,
      colHeight:colHeight,
      imagesOffset: imgOffset,
    });
  }
}




