// tslint:disable:object-literal-sort-keys
// tslint:disable:no-console
// tslint:max-classes-per-file
import * as React from "react";
import './LightBox.css';
import { ImgData } from './Model';
import * as imgdata from '../img/img.json';

class LightBoxProp{
    public imgsrc:string;
    public onClose:()=>void;
}

class LightBoxState{
    public imgsrc:string;
    public allimage:ImgData[];
    public curimg:string;
    public show:boolean;
}

export class LightBox  extends React.Component<LightBoxProp,LightBoxState>{
    private m_touchPos:any;
    private m_touchId:number = -1;

    public constructor(prop:LightBoxProp){
        super(prop);

        let curimg = prop.imgsrc;

        this.state = {
            imgsrc : prop.imgsrc,
            allimage: imgdata,
            curimg: curimg,
            show:false,
        };
    }

    public componentDidMount(){
        const lbimg = document.getElementById("lboximg");
        if(lbimg == null )return;
        lbimg.addEventListener('touchstart',this.onTouchBegin.bind(this),{passive:true});
        lbimg.addEventListener('touchend',this.onTouchEnd.bind(this),{passive:true});


        window.addEventListener('mousewheel',this.onScroll.bind(this),{passive:true});
    }


    public onScroll(e:MouseWheelEvent){
        if(this.state.show){
            this.moveImg(e.deltaY >0 ?1:-1);
        }
    }


    public componentWillUnmount(){
        const lbimg = document.getElementById("lboximg");
        if(lbimg == null )return;

        lbimg.removeEventListener('touchstart',this.onTouchBegin.bind(this));
        lbimg.removeEventListener('touchend',this.onTouchEnd.bind(this));

        window.removeEventListener('scroll',this.onScroll.bind(this));
    }

    private onTouchBegin(e:TouchEvent){
        let touch = e.touches[0];
        this.m_touchId = touch.identifier;
        this.m_touchPos = [touch.pageX,touch.pageY];
    }

    private onTouchEnd(e:TouchEvent){
        if(this.m_touchId == -1) return;
        let touches = e.changedTouches;
        let touch = null;

        for(let i=0,len = touches.length;i<len ;i++){
            if(touches[i].identifier == this.m_touchId){
                touch = touches[i];
                break;
            }
        }

        if(touch == null){
            return;
        }

        this.m_touchId = -1;
        let prevpos = this.m_touchPos;
        let offset = touch.pageX - prevpos[0];
        if(Math.abs(offset) > 100){
            this.moveImg(offset < 0 ? -1:1);
        }
    }

    public render(){

        let curimg = this.state.curimg;
        if(curimg == undefined){
            curimg = imgdata[0][0].file;
        }

        const style = {
            display: this.state.show?"unset":"none",
        };

        return (
            <div style={style} className="LightBox" id="lbox">
                <div className="lbox-bg"></div>
                <div className="lbox-cnt">
                    <div onClick={this.prev} className="lbox-btn lbox-btnl"></div>
                    <div className="lbox-wrap">
                        <img id="lboximg" onClick={this.closeBox} className="lbox-img" src={ (curimg !== ''? require("../artworks/" +curimg ): "")} />
                    </div>
                    <div onClick={this.next} className="lbox-btn lbox-btnr"></div>
                </div>
            </div>
        )

    }

    public prev = ()=>{
        this.moveImg(-1);
    }

    public next = ()=>{
        this.moveImg(1);
        
    }

    private moveImg = (offset:number)=>{
        const curimg = this.state.curimg;
        const allimg = this.state.allimage;

        let imgindex = -1;
        for(let i=0,len = allimg.length;i<len;i++){
            let img = allimg[i];
            if(img.file === curimg){
                imgindex = i;
                break;
            }
        }
        if(imgindex ==-1) return;
        imgindex += offset;
        if(imgindex <0 || imgindex >= allimg.length) return;
        this.showImg(allimg[imgindex].file);
    }

    public showImg = (img:string)=>{
        this.setState({
            show: true,
            curimg: img,
        })
    }

    public closeBox = ()=>{
        const cb = this.props.onClose;
        if(cb != null) cb();
        this.setState({
            show: false
        });
    }
    
}


