import * as React from 'react';
import './App.css';

import Gallery from './Components/Gallery';
import logo from './img/favicon.png';
import { LightBox } from './Components/LightBox';


class App extends React.Component {

  public render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Yemi's Artworks</h1>
          <h5 className="App-links" ><a href="https://yemi.me">blog</a> <a href="https://yemi.me/about/">about</a></h5>
        </header>
        <div className="Content">
          <Gallery ref="gallery" onImgSel={this.onImgSel.bind(this)} />
        </div>
        <LightBox onClose={this.onLightboxClose} ref="lbox" imgsrc="" />
        <div className="cpy-txt">
          Copyright  2018 Yemi
        </div>
      </div>
    );
  }

  public onImgSel(imgfile:string){
    var self =this;
    setTimeout(() => {
      const lbox= self.refs['lbox'] as LightBox;
      lbox.showImg(imgfile);
      self.enableScroll(false);
  
      const gallery = self.refs['gallery'];
      if(gallery != null){
        (gallery as Gallery).blur(true);
      }
    }, 250);
  }

  public onLightboxClose = ()=>{
    this.enableScroll(true);
    const gallery = this.refs['gallery'];
    if(gallery != null){
      (gallery as Gallery).blur(false);
    }
  }

  public enableScroll(scroll:boolean){
    const html = document.documentElement;
    if(html){
      html.style.overflow= scroll?"auto":"hidden";
    }
  }
}



export default App;



